.fill {
  fill:none;
  fill-rule:evenodd;
}

.stroke {
  stroke:#ffffff;
}

.stroke:hover {
  stroke:#ff0000;
}

.disabled, .disabled:hover {
  stroke:#999999;
}

.strokeLine {
  stroke-linecap:round;
  stroke-linejoin:round;
}

.explore {
  stroke:#fff;
}

.explore .a {
  fill:none;
  stroke-linecap:round;
  stroke-linejoin:round;
}

.explore .b {
  fill:#163567;
}

