.t1 {
  stroke:#ffffff;
}

.t1.disabled {
  stroke: grey;
}

.t1 .a{
  fill:#255755;
}
  
.t1 .b{
  fill:none;
}

.t1 .a, .t1 .b{
  stroke-linecap:round;
  stroke-linejoin:round;
}

.t2 {
  stroke:#ffffff;
}

.t2 .a{
  fill:#255755;
}

.t2 .a,.t2 .b,.t2 .c{

  stroke-linecap:round;
  stroke-linejoin:round;
}

.t2 .b{
  fill:none;
}
.t2 .c{
  fill:#fff;
}
  

